

.profile{
    display:flex;
    justify-content:center;
    gap:60px;
    margin-top:120px;
    align-items: center;
    z-index: 0;
    /* border: 1px solid red; */
}

.image{
    border-radius:8%;
    width:300px;
    height:350px;
    /* border:3px solid white */
}

.card1{
    color:white;
    align-items:center;
    text-align:left
}

.card1>p{
    color:yellow;
    font-size:20px;
    margin-top:60px
}

.card1>h1{
    font-size:45px;
    margin-top:0px
}

.card1>button{
    background-color:darkslateblue;
    cursor:pointer;
    color:white;
    border:2px solid white;
    padding:10px 10px;
    font-size:20px;
    margin-top:40px
}

@media screen and (max-width: 592px) {
    .profile{
        display: block;
        text-align: center;
        
    }
    .card1{
        text-align:center;
        width: 100%;
    }

    .image{
        border-radius:60%;
        width:300px;
        height:300px;
        margin: auto;
        /* border:3px solid white */
    }
  }